import { useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import {
  getTabOverrides,
  Tab,
  Tabs as BaseTabs,
  TabTitle,
} from '@jifflenow/brightui/tabs-motion'
import { tabsOverrides, tabOverrides } from 'ui-components/DrawerPane/overrides'
import { Participants } from './Participants'
import { MeetingInfoTab } from './MeetingInfoTab'
import { TabsProps } from './types'

const messages = defineMessages({
  attendeesLabel: {
    defaultMessage: 'Participants',
    description: 'Tab heading',
  },
  meetingInfoLabel: {
    defaultMessage: 'Meeting Information',
    description: 'Tab heading',
  },
})

enum TabKeys {
  PARTICIPANTS = 'participants',
  MEETING_INFO = 'meetingInfo',
}

export const Tabs: VFC<TabsProps> = ({ linkDetails }) => {
  const { formatMessage } = useIntl()

  const [activeKey, setActiveKey] = useState<TabKeys>(TabKeys.PARTICIPANTS)

  return (
    <BaseTabs
      activeKey={activeKey}
      onChange={({ activeKey }) => {
        setActiveKey(activeKey as TabKeys)
      }}
      activateOnFocus={false}
      overrides={tabsOverrides}
    >
      <Tab
        key={TabKeys.PARTICIPANTS}
        title={<TabTitle>{formatMessage(messages.attendeesLabel)}</TabTitle>}
        overrides={getTabOverrides(tabOverrides)}
        data-pendo={`tab-${TabKeys.PARTICIPANTS}`}
      >
        <Participants linkDetails={linkDetails} />
      </Tab>
      <Tab
        key={TabKeys.MEETING_INFO}
        title={<TabTitle>{formatMessage(messages.meetingInfoLabel)}</TabTitle>}
        overrides={getTabOverrides(tabOverrides)}
        data-pendo={`tab-${TabKeys.MEETING_INFO}`}
      >
        <MeetingInfoTab {...linkDetails} />
      </Tab>
    </BaseTabs>
  )
}
