import { FC } from 'react'
import ContentLoader, { IContentLoaderProps } from 'react-content-loader'
import { useStyletron } from '@jifflenow/brightui'
import { useAppSource } from 'providers/AppSourceProvider'

export type LoadingShimmerProps = IContentLoaderProps

export const LoadingShimmer: FC<LoadingShimmerProps> = ({
  children,
  ...restProps
}) => {
  const { platform } = useAppSource()
  const [, { colors }] = useStyletron()

  const contentLoaderOptions: LoadingShimmerProps = {
    speed: 2,
    primaryColor: colors.mono200,
    secondaryColor: colors.mono300,
    height: '140',
    ...restProps,
    animate: platform !== 'mobile',
  }

  return <ContentLoader {...contentLoaderOptions}>{children}</ContentLoader>
}
