import { FC, useState } from 'react'
import { RowClickedEvent } from '@ag-grid-community/core'
import { List as AgGridList } from 'components/ag-grid'
import { components } from 'types/openapi'
import { LinkDetails } from '../LinkDetails'
import { scheduleMeeting } from '../scheduleMeeting'
import * as constants from './constants'
import { NoRowsAndError } from './NoRowsAndError'
import { InlineActions } from './InlineActions'
import { StatusCell } from './StatusCell'
import { LinkType } from './LinkTypeCell'
import { ListProps } from './types'

type SharedLinks = components['schemas']['SharedLinks']['data']
type Link = components['schemas']['SharedLink']

export const List: FC<ListProps<SharedLinks>> = props => {
  const [link, setLink] = useState<Link>()

  const onRowClicked = ({ data, event }: RowClickedEvent) =>
    !(event?.target as Element).closest('[col-id=actions]') && setLink(data)

  const handleMeetingClick = (meetingUuid: string) => {
    const {
      relationships: {
        location: { systemName: eventName, uuid },
      },
    } = link!
    window.open(
      `${window.location.origin}/${eventName}/meeting_request/${meetingUuid}/edit?current_location_uuid=${uuid}`,
      '_blank'
    )
  }

  return (
    <>
      <AgGridList
        {...props}
        constants={constants}
        frameworkComponents={{
          actionCell: InlineActions,
          linkTypeCell: LinkType,
          statusCell: StatusCell,
          noRowsAndError: NoRowsAndError,
        }}
        onRowClicked={onRowClicked}
      />
      {link && (
        <LinkDetails
          link={link}
          onMeetingClick={handleMeetingClick}
          onScheduleMeeting={() => scheduleMeeting(link)}
          onClose={() => setLink(undefined)}
          isWebApp
        />
      )}
    </>
  )
}
