import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Accordion, Panel } from '@jifflenow/brightui/accordion'
import { TimeOutlined } from '@jifflenow/brightui/icon'
import {
  formatDateRange,
  formatDateTimeRange,
  formatToStartOfTheDay,
} from 'utils/dates'
import {
  Column,
  InfoDescription,
  InfoLabel,
  MeetingSlot,
} from './styled-components'
import { accordionOverride } from './overrides'
import { AvailableSlotsProps } from './types'
import { LinkInfo } from './LinkDetails'

const messages = defineMessages({
  slotsLabel: {
    defaultMessage: '{count} {count, plural, one {slot} other {slots}}',
    description: 'available slots label',
  },
})

const getRange = (timeRanges: AvailableSlotsProps['timeRanges'] = []) => ({
  startDate: timeRanges.map(({ startTime }) => startTime).sort()[0],
  endDate: timeRanges
    .map(({ endTime }) => endTime)
    .sort()
    .reverse()[0],
})

const RangeView: VFC<AvailableSlotsProps> = ({ timeRanges = [], timeZone }) => {
  const { startDate, endDate } = getRange(timeRanges)

  return (
    <>
      <InfoLabel>
        {formatDateRange(
          formatToStartOfTheDay(startDate),
          formatToStartOfTheDay(endDate)
        )}
      </InfoLabel>
      <InfoDescription>{timeZone}</InfoDescription>
    </>
  )
}

const AccordionView: VFC<AvailableSlotsProps> = ({
  timeRanges = [],
  timeZone,
}) => {
  const { formatMessage } = useIntl()

  const { startDate, endDate } = getRange(timeRanges)

  const getAccordionHeader = () => (
    <Column>
      <InfoLabel>
        {formatDateRange(
          formatToStartOfTheDay(startDate),
          formatToStartOfTheDay(endDate)
        )}
        &nbsp;&ndash;&nbsp;
        {formatMessage(messages.slotsLabel, { count: timeRanges.length })}
      </InfoLabel>
      <InfoDescription>{timeZone}</InfoDescription>
    </Column>
  )

  return (
    <Accordion overrides={accordionOverride}>
      <Panel title={getAccordionHeader()}>
        {timeRanges.map(({ startTime, endTime }, i) => (
          <MeetingSlot key={i}>
            {formatDateTimeRange(new Date(startTime), new Date(endTime))}
          </MeetingSlot>
        ))}
      </Panel>
    </Accordion>
  )
}

export const AvailableSlots: VFC<AvailableSlotsProps> = props => {
  const { timeRanges = [] } = props

  const isRange =
    timeRanges?.length === 1 &&
    /^\d{4}-\d{2}-\d{2}$/u.test(timeRanges[0].startTime)

  return timeRanges.length ? (
    <LinkInfo Icon={TimeOutlined}>
      {isRange ? <RangeView {...props} /> : <AccordionView {...props} />}
    </LinkInfo>
  ) : null
}
