import { useCallback, useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { FieldRenderProps } from 'react-final-form'
import { Textarea } from '@jifflenow/brightui/textarea'
import { Select, Value } from '@jifflenow/brightui/select'
import { Field, Validation } from 'ui-components/Field'
import { FormControlStyled } from './styled-components'

const messages = defineMessages({
  otherReasonPlaceholder: {
    defaultMessage: 'Type here',
    description: 'Placeholder text',
  },
})

const OtherReasons: VFC<FieldRenderProps<string>> = ({
  input,
  meta: { touched, error },
}) => {
  const { formatMessage } = useIntl()

  return (
    <FormControlStyled error={touched && error}>
      <Textarea
        {...input}
        placeholder={formatMessage(messages.otherReasonPlaceholder)}
        inputProps={{ 'data-pendo': 'meetings/cancel-other-reasons' }}
      />
    </FormControlStyled>
  )
}

export const CancellationReasonField: VFC<FieldRenderProps<string>> = ({
  input,
  meta: { touched, error },
  options,
  label,
}) => {
  const [value, setValue] = useState<Value>([])

  const handleChange = useCallback((value: Value) => {
    setValue(value)
    input.onChange(value[0].id)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {
    handleChange([options[0]])
  }, [handleChange, options])

  return (
    <>
      <FormControlStyled label={label} error={touched && error}>
        <Select
          {...input}
          clearable={false}
          id="reason"
          options={options}
          value={value}
          onChange={({ value }: { value: Value[] }) => handleChange(value)}
          data-pendo="shared-links/cancel-reasons"
        />
      </FormControlStyled>

      {input.value === 'other' ? (
        <Field
          name="other"
          render={OtherReasons}
          validations={[Validation.REQUIRED]}
        />
      ) : null}
    </>
  )
}
