import { VFC, useState } from 'react'
import { useMutation } from 'react-query'
import { defineMessages, useIntl } from 'react-intl'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { renewInboundLinkMutationFn, useSummitV1Client } from 'rest'
import { PrimaryActionButton } from 'ui-components/DrawerPane'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { Remind } from '../common'
import { FooterActionProps } from './types'

const messages = defineMessages({
  remindSuccessMessage: {
    defaultMessage: 'Reminded participants successfully',
    description: 'Success message',
  },
  renewLinkSuccessMessage: {
    defaultMessage: 'Link renewed successfully',
    description: 'Success message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  renewLabel: {
    defaultMessage: 'Renew',
    description: 'Button label',
  },
  remindLabel: {
    defaultMessage: 'Remind',
    description: 'Button label',
  },
  editLabel: {
    defaultMessage: 'Edit',
    description: 'Button label',
  },
})

export const FooterAction: VFC<FooterActionProps> = ({
  linkDetails,
  primaryActions,
  externalAttendees,
  onLinkEdit,
}) => {
  const { formatMessage } = useIntl()
  const [showRemind, setShowRemind] = useState(false)

  const {
    attributes: {
      uuid: token,
      relationships: {
        location: { systemName: eventName },
      },
    },
  } = linkDetails
  const { canRemindParticipants, canRenewExpiry, canEdit } = primaryActions

  const summitClient = useSummitV1Client(eventName)

  const handleError = () => {
    showErrorToast(formatMessage(messages.genericError))
  }

  const params = { client: summitClient, token }

  const { mutate: renewLink, isLoading: renewInProgress } = useMutation(
    'renewLink',
    renewInboundLinkMutationFn,
    {
      onSuccess: () => {
        showSuccessToast(formatMessage(messages.renewLinkSuccessMessage))
      },
      onError: handleError,
    }
  )

  return (
    <>
      {renewInProgress && <ModalSpinner />}
      {canRemindParticipants ? (
        <>
          {showRemind && (
            <Remind
              token={token}
              eventName={eventName}
              attendees={externalAttendees}
              onClose={() => setShowRemind(false)}
            />
          )}
          <PrimaryActionButton
            onClick={() => setShowRemind(true)}
            dataPendo="shared-links/remind-participants"
          >
            {formatMessage(messages.remindLabel)}
          </PrimaryActionButton>
        </>
      ) : canRenewExpiry ? (
        <PrimaryActionButton
          onClick={() => renewLink(params)}
          dataPendo="shared-links/renew-link"
        >
          {formatMessage(messages.renewLabel)}
        </PrimaryActionButton>
      ) : canEdit ? (
        <PrimaryActionButton
          onClick={() => onLinkEdit?.()}
          dataPendo="shared-links/edit-link"
        >
          {formatMessage(messages.editLabel)}
        </PrimaryActionButton>
      ) : null}
    </>
  )
}
