import { useState, useEffect } from 'react'
import { TextFilter } from '@jifflenow/brightui/filter'
import { FilterItem, Options } from 'components/ag-grid'
import { getEngagementTypes, useQuery } from 'rest'
import { useDebounce } from 'utils/debounce'
import { useParams } from 'react-router-dom'
import { useEventLocation } from 'providers/LocationProvider'
import { FilterProps } from './types'

export const EngagementTypesFilter = ({
  value,
  label,
  onChange,
}: FilterProps) => {
  const { eventName } = useParams<{ eventName: string }>()
  const { id: locationId } = useEventLocation().location!
  const [searchText, setSearchText] = useState('')
  const [options, setOptions] = useState<Options[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const search = useDebounce(searchText)

  const { refetch: fetchEngagementTypes } = useQuery(
    [
      'getEngagementTypeFilterOption',
      { term: search, locations: [locationId] },
    ],
    getEngagementTypes({ eventName }),
    {
      enabled: false,
      onSuccess: ({ data }) => {
        setOptions(data.map(({ name, uuid }) => ({ id: uuid, label: name })))
        setIsLoading(false)
      },
    }
  )

  useEffect(() => {
    !search ? setOptions([]) : fetchEngagementTypes()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [search])

  return (
    <FilterItem>
      <TextFilter
        label={label}
        onChange={onChange}
        options={options}
        value={value}
        asyncOptions
        searchText={searchText}
        onSearch={setSearchText}
        isLoading={isLoading}
      />
    </FilterItem>
  )
}
