import { VFC } from 'react'
import { defineMessages, useIntl, FormattedMessage } from 'react-intl'
import { Input } from '@jifflenow/brightui/input'
import { Search } from '@jifflenow/brightui/icon'
import {
  Container,
  RightContainer,
  InputControl,
  Root,
  Title,
} from './styled-components'
import { HeaderProps } from './types'

const messages = defineMessages({
  search: {
    defaultMessage: 'Search',
    description: 'Placeholder text',
  },
})

export const Header: VFC<HeaderProps> = ({ itemCount, onSearch, search }) => {
  const { formatMessage } = useIntl()

  return (
    <Root>
      <Container>
        <Title>
          <FormattedMessage
            defaultMessage="Shared links  {count, number}"
            description="Section title"
            values={{ count: itemCount }}
          />
        </Title>
      </Container>
      <RightContainer>
        <InputControl>
          <Input
            type="search"
            value={search}
            IconAfter={Search}
            onChange={onSearch}
            placeholder={formatMessage(messages.search)}
          />
        </InputControl>
      </RightContainer>
    </Root>
  )
}
