import { FC, VFC } from 'react'
import { DateTime } from 'luxon'
import { defineMessages, useIntl } from 'react-intl'
import { IconProps } from '@jifflenow/brightui/icon'
import { LinkButton, KIND } from '@jifflenow/brightui/button'
import { Accordion, Panel } from '@jifflenow/brightui/accordion'
import { LinkStatusProps, Status } from './types'
import { LinkInfo } from './LinkDetails'
import {
  ColumnFullWidth,
  InfoContent,
  InfoIconRoot,
  InfoLabel,
  InfoRoot,
  Row,
  CancelledLabel,
} from './styled-components'
import { CalendarCancelled, CalendarPending, CalendarScheduled } from './icons'
import { MeetingLinkProps } from './types'
import {
  accordionOverride,
  getMeetingLinkButtonOverrides,
  linkInfoIconOverride,
} from './overrides'

const messages = defineMessages({
  expiresOnLabel: {
    defaultMessage: 'Expires on {date}',
    description: 'Link Expiry information',
  },
  expiredLabel: {
    defaultMessage: 'Expired on {date}',
    description: 'Link expired label',
  },
  scheduledLabel: {
    defaultMessage: 'Scheduled',
    description: 'Link status label',
  },
  cancelledLabel: {
    defaultMessage: 'Cancelled',
    description: 'Link status label',
  },
  meetingLabel: {
    defaultMessage: 'Meeting #{id}',
    description: 'Meeting id label',
  },
  meetingLabelWithCount: {
    defaultMessage:
      '{count} {count, plural, one {meeting} other {meetings}} (of {meetingsQuota})',
    description: 'Meeting with count label',
  },
  cancelledMeetingLabelWithCount: {
    defaultMessage:
      '{count} {count, plural, one {meeting} other {meetings}} cancelled',
    description: 'Cancelled Meeting with count label',
  },
})

const formatDate = (date: string) =>
  DateTime.fromISO(date).toLocaleString(DateTime.DATETIME_FULL)

const iconMap: Record<Status, FC<IconProps>> = {
  scheduled: CalendarScheduled,
  pending: CalendarPending,
  cancelled: CalendarCancelled,
  expired: CalendarCancelled,
}

const MeetingLink: VFC<MeetingLinkProps> = ({
  meetingId: id,
  showAsBlock = false,
  onClick,
  status,
}) => {
  const { formatMessage } = useIntl()

  return (
    <LinkButton
      size={14}
      type="button"
      kind={KIND.minimal}
      overrides={getMeetingLinkButtonOverrides(showAsBlock)}
      data-pendo="shared-links-detail/meeting-link"
      onClick={onClick}
    >
      {formatMessage(messages.meetingLabel, { id })}&nbsp;
      {status === 'cancelled' ? (
        <CancelledLabel>
          {` (${formatMessage(messages.cancelledLabel, { id })})`}
        </CancelledLabel>
      ) : null}
    </LinkButton>
  )
}

export const LinkStatus: VFC<LinkStatusProps> = ({
  linkDetails,
  onMeetingClick,
}) => {
  const { formatMessage } = useIntl()
  const {
    attributes: {
      expiredOn,
      status,
      meetingsQuota,
      scheduledMeetings,
      numberOfScheduledMeetings,
    },
  } = linkDetails

  const Icon = iconMap[status]

  if (meetingsQuota > 1) {
    if (status === 'expired' || status === 'scheduled') {
      return (
        <InfoRoot>
          <ColumnFullWidth>
            <Row>
              <InfoIconRoot>
                <Icon size={14} overrides={linkInfoIconOverride} />
              </InfoIconRoot>
              <InfoContent>
                <InfoLabel>
                  {status === 'expired'
                    ? formatMessage(messages.expiredLabel, {
                        date: formatDate(expiredOn),
                      })
                    : null}
                  {status === 'scheduled' ? (
                    <>
                      {formatMessage(messages.scheduledLabel)}
                      &nbsp;&bull;&nbsp;
                      {formatMessage(messages.expiresOnLabel, {
                        date: formatDate(expiredOn),
                      })}
                    </>
                  ) : null}
                </InfoLabel>
              </InfoContent>
            </Row>

            {scheduledMeetings && (
              <Accordion overrides={accordionOverride}>
                <Panel
                  title={
                    <InfoLabel>
                      {formatMessage(messages.meetingLabelWithCount, {
                        count: numberOfScheduledMeetings,
                        meetingsQuota,
                      })}
                    </InfoLabel>
                  }
                >
                  {scheduledMeetings.map(({ id, uuid, status }) => (
                    <MeetingLink
                      key={id}
                      meetingId={id}
                      status={status}
                      showAsBlock
                      onClick={() => onMeetingClick?.(uuid)}
                    />
                  ))}
                </Panel>
              </Accordion>
            )}
          </ColumnFullWidth>
        </InfoRoot>
      )
    }
  }

  if (status === 'pending' || status === 'expired') {
    return (
      <LinkInfo Icon={Icon}>
        <InfoLabel>
          {formatMessage(
            messages[status === 'pending' ? 'expiresOnLabel' : 'expiredLabel'],
            {
              date: formatDate(expiredOn),
            }
          )}
        </InfoLabel>
      </LinkInfo>
    )
  }

  if (status === 'scheduled') {
    const activeMeeting = scheduledMeetings?.find(
      ({ status }) => status !== 'cancelled'
    )

    const cancelledMeetings = scheduledMeetings?.filter(
      ({ status }) => status === 'cancelled'
    )

    return (
      <>
        <LinkInfo Icon={Icon}>
          <InfoLabel>
            {formatMessage(messages.scheduledLabel)}&nbsp;
            {activeMeeting && (
              <MeetingLink
                meetingId={activeMeeting.id}
                status={activeMeeting.status}
                onClick={() => onMeetingClick?.(activeMeeting.uuid)}
              />
            )}
          </InfoLabel>
        </LinkInfo>
        {cancelledMeetings?.length ? (
          <InfoRoot>
            <Accordion overrides={accordionOverride}>
              <Panel
                title={
                  <InfoLabel>
                    {formatMessage(messages.cancelledMeetingLabelWithCount, {
                      count: cancelledMeetings.length,
                    })}
                  </InfoLabel>
                }
              >
                {cancelledMeetings.map(({ id, uuid }) => (
                  <MeetingLink
                    key={id}
                    meetingId={id}
                    showAsBlock
                    onClick={() => onMeetingClick?.(uuid)}
                  />
                ))}
              </Panel>
            </Accordion>
          </InfoRoot>
        ) : null}
      </>
    )
  }

  return (
    <LinkInfo Icon={Icon}>
      <InfoLabel>{formatMessage(messages.cancelledLabel)}</InfoLabel>
    </LinkInfo>
  )
}
