import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { ICellRendererParams } from '@ag-grid-community/core'
import { components } from 'types/openapi'

type LinkType = components['schemas']['InboundLinkType']

const messages = defineMessages<LinkType>({
  singleUse: {
    defaultMessage: 'Single-use',
    description: 'Link type cell value',
  },
  multiUse: {
    defaultMessage: 'Multi-use',
    description: 'Link type cell value',
  },
})

export const LinkType: VFC<ICellRendererParams> = prop => {
  const { formatMessage } = useIntl()

  return <>{formatMessage(messages[prop.value as LinkType])}</>
}
