import { useState, useEffect } from 'react'
import { useParams } from 'react-router-dom'
import { useQuery } from 'react-query'
import { TextFilter } from '@jifflenow/brightui/filter'
import { FilterItem, Options } from 'components/ag-grid'
import { getMeetingFilterOptions } from 'rest'
import { useDebounce } from 'utils/debounce'
import { FilterProps } from './types'

export const ParticipantsFilter = ({ value, label, onChange }: FilterProps) => {
  const { eventName } = useParams<{ eventName: string }>()
  const [searchText, setSearchText] = useState('')
  const [options, setOptions] = useState<Options[]>([])

  const search = useDebounce(searchText)

  const { isLoading, refetch } = useQuery(
    [
      'meetingFilterOption',
      { field: 'attendee_field', response_format: 'object', term: search },
    ],
    getMeetingFilterOptions({ eventName }),
    {
      enabled: false,
      onSuccess: data => {
        setOptions(
          (data?.data ?? []).map(({ name, uuid }) => ({
            id: uuid,
            label: name,
          }))
        )
      },
    }
  )

  useEffect(() => {
    !search ? setOptions([]) : refetch()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [search])

  return (
    <FilterItem>
      <TextFilter
        label={label}
        onChange={onChange}
        options={options}
        value={value}
        asyncOptions
        searchText={searchText}
        onSearch={setSearchText}
        isLoading={isLoading}
      />
    </FilterItem>
  )
}
