import { ButtonOverrides } from '@jifflenow/brightui/button'
import { AccordionOverrides, SharedProps } from '@jifflenow/brightui/accordion'
import { IconOverrides } from '@jifflenow/brightui/icon'

export const accordionOverride: AccordionOverrides<SharedProps> = {
  Root: {
    style: ({ $theme: { colors } }) => ({
      borderTopWidth: '1px',
      borderBottomWidth: '1px',
      borderLeftWidth: '1px',
      borderRightWidth: '1px',
      borderTopStyle: 'solid',
      borderLeftStyle: 'solid',
      borderRightStyle: 'solid',
      borderBottomStyle: 'solid',
      borderTopColor: colors.mono400,
      borderBottomColor: colors.mono400,
      borderLeftColor: colors.mono400,
      borderRightColor: colors.mono400,
    }),
  },
  Header: {
    style: ({ $theme: { colors, sizing } }) => ({
      backgroundColor: colors.mono200,
      color: colors.mono900,
      paddingLeft: sizing.scale500,
      paddingRight: sizing.scale500,
      borderBottomWidth: '1px',
      borderBottomStyle: 'solid',
      borderBottomColor: colors.mono400,
      ':hover': { color: colors.mono900 },
    }),
  },
  PanelContainer: {
    style: {
      borderBottomStyle: 'none',
    },
  },
  Content: {
    style: ({ $theme: { sizing, colors } }) => ({
      paddingLeft: sizing.scale500,
      paddingRight: sizing.scale500,
      paddingBottom: sizing.scale400,
      paddingTop: sizing.scale400,
      backgroundColor: colors.mono200,
    }),
  },
}

export const getMeetingLinkButtonOverrides = (
  showAsBlock: boolean
): ButtonOverrides => ({
  BaseButton: {
    style: ({ $theme: { colors, sizing, typography } }) => ({
      ...typography.LabelSmall,
      ...(showAsBlock
        ? {
            display: 'block',
            marginBottom: sizing.scale300,
            backgroundColor: colors.mono200,
          }
        : null),
      paddingLeft: 0,
      paddingRight: 0,
      paddingTop: 0,
      paddingBottom: 0,
    }),
  },
})

export const linkInfoIconOverride: IconOverrides = {
  Svg: {
    style: ({ $theme: { colors } }) => ({
      color: colors.mono700,
    }),
  },
}

export const optionalIconOverride: IconOverrides = {
  Svg: {
    style: ({ $theme: { sizing, colors } }) => ({
      marginRight: sizing.scale100,
      color: colors.mono700,
    }),
  },
}
