import { defineMessages, useIntl } from 'react-intl'
import { useEventLocation } from 'providers/LocationProvider'
import { useMe } from 'providers/MeProvider'
import { DocumentTitle } from 'ui-components/DocumentTitle'

const messages = defineMessages({
  linkList: {
    defaultMessage: 'Link List',
    description: 'Page title',
  },
})

export const LinkListTitle = () => {
  const { formatMessage } = useIntl()
  const { companyName } = useMe().meta
  const { displayName: eventName } =
    useEventLocation().location?.attributes ?? {}

  return (
    <DocumentTitle
      title={`${formatMessage(
        messages.linkList
      )} - ${eventName} - ${companyName}`}
    />
  )
}
