import { VFC } from 'react'
import {
  ActionContainer,
  DrawerPaneBackButton,
  DrawerTitle,
  HeaderColumn,
  HeaderContainer,
  HeaderRow,
} from 'ui-components/DrawerPane'
import { AutoTooltip } from 'ui-components/AutoTooltip'
import { useDrawer } from 'ui-components/Drawer'
import { HeaderProps } from './types'
import { LinkActionsMenu } from './LinkActionsMenu'
import { LinkActionsMenuProps } from './types'
import { LinkStatus } from './styled-components'

export const Header: VFC<HeaderProps> = ({
  linkDetails,
  secondaryActions,
  onLinkEdit,
  onScheduleMeeting,
}) => {
  const { closeDrawer } = useDrawer()

  const {
    id,
    attributes: {
      primaryId,
      meetingWith,
      link,
      status,
      relationships: {
        location: {
          systemName: eventName,
          displayName,
          uuid: currentLocationUuid,
        },
        attendees,
        engagementType: { uuid: engagementTypeUuid },
      },
    },
  } = linkDetails

  const actionsMenuProps: LinkActionsMenuProps = {
    id,
    link,
    actions: secondaryActions,
    eventName,
    status,
    onLinkEdit,
    onScheduleMeeting,
    attendees,
    currentLocationUuid,
    engagementTypeUuid,
  }

  return (
    <HeaderContainer>
      <DrawerPaneBackButton onClick={() => closeDrawer()} />
      <HeaderColumn>
        <HeaderRow>
          <AutoTooltip content={meetingWith} showArrow>
            <DrawerTitle>{meetingWith}</DrawerTitle>
          </AutoTooltip>
        </HeaderRow>
        <HeaderRow>
          &#35;{primaryId}&nbsp;&#124;&nbsp;{displayName}
        </HeaderRow>
      </HeaderColumn>

      <ActionContainer>
        <LinkActionsMenu {...actionsMenuProps} />
      </ActionContainer>
      <LinkStatus $status={status} />
    </HeaderContainer>
  )
}
