import { VFC, useState, useEffect, ChangeEvent } from 'react'
import { stringifyUrl } from 'query-string'
import { defineMessages, useIntl } from 'react-intl'
import { useQuery } from 'react-query'
import { GridApi } from '@ag-grid-community/core'
import { getSharedLinksQueryFn } from 'rest'
import { StatefulTooltip, PLACEMENT } from '@jifflenow/brightui/tooltip'
import { Pagination, KIND } from '@jifflenow/brightui/pagination'
import {
  getTabOverrides,
  Tab,
  Tabs,
  TabTitle,
} from '@jifflenow/brightui/tabs-motion'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { IconButton, SIZE } from '@jifflenow/brightui/button'
import { Export } from '@jifflenow/brightui/icon'
import { useRouteMatch, useParams, useLocation } from 'react-router-dom'
import { useEventLocation } from 'providers/LocationProvider'
import { useDebounce } from 'utils/debounce'
import { Paths } from 'components/Routes/Paths'
import { useSelection, PAGE_SIZE } from 'components/ag-grid'
import { components } from 'types/openapi'
import { tabOverrides, tabsOverrides } from './overrides'
import { useSharedLinksPreferences } from './SharedLinkPreferencesProvider'
import { List } from './List'
import { Header } from './Header'
import { Filters } from './Filters'
import { LinkListTitle } from './LinkListTitle'
import { Footer, SubHeaderRoot } from './styled-components'

type GeneralNameAndUuidFormat = components['schemas']['GeneralNameAndUuidFormat']

enum TabKeys {
  LINKS = 'links',
  MEETINGS = 'meetings',
}

const getUuids = ({ uuid }: GeneralNameAndUuidFormat) => uuid

const aria = defineMessages({
  labelExportLinks: {
    defaultMessage: 'Export links',
    description: 'Aria label',
  },
})

const messages = defineMessages({
  labelExportLinks: {
    defaultMessage: 'Export',
    description: 'Button label',
  },
})

const tabTitles = defineMessages<TabKeys>({
  links: {
    defaultMessage: 'Shared links',
    description: 'Tab title',
  },
  meetings: {
    defaultMessage: 'Meetings',
    description: 'Tab title',
  },
})

export const LinkList: VFC = () => {
  const { formatMessage } = useIntl()

  const [activeTab, setActiveTab] = useState<TabKeys>(TabKeys.LINKS)
  const [gridApi, setGridApi] = useState<GridApi | null>(null)
  const [search, setSearch] = useState<string>('')
  const searchTerm = useDebounce(search.trim(), 500)
  const { deselectAll } = useSelection()
  const [pageNumber, setPageNumber] = useState<number>(1)

  const { eventName } = useParams<{ eventName: string }>()

  const location = useEventLocation().location!

  const matchLinkListPath = useRouteMatch({
    exact: true,
    path: Paths.LINKS.path,
  })

  const resetToFirstPage = () => {
    setPageNumber(1)
    deselectAll()
  }

  const listPreferences = useSharedLinksPreferences()

  const {
    sortBy,
    statuses,
    linkTypes,
    participants,
    engagementTypes,
  } = listPreferences.preferences

  const linkListQueryParams = {
    page: { size: PAGE_SIZE, number: pageNumber },
    search_term: searchTerm,
    'filter[emeetLocationUuid][]': [
      new URLSearchParams(useLocation().search).get('location')!,
    ],
    sortBy,
    'statuses[]': statuses,
    'linkTypes[]': linkTypes,
    'participants[]': participants.map(getUuids),
    'engagementTypes[]': engagementTypes.map(getUuids),
  }

  const { isLoading, data, refetch: fetchList } = useQuery(
    ['sharedLinks', linkListQueryParams],
    getSharedLinksQueryFn({ eventName }),
    {
      onError: () => gridApi?.showNoRowsOverlay(),
    }
  )

  const { size = PAGE_SIZE, total = 0 } = data?.meta.page ?? {}

  useEffect(() => {
    isLoading ? gridApi?.showLoadingOverlay() : gridApi?.hideOverlay()
  }, [gridApi, isLoading])

  return (
    <>
      {matchLinkListPath && <LinkListTitle />}
      <Header
        search={search}
        itemCount={total}
        onSearch={(e: ChangeEvent<HTMLInputElement>) => {
          resetToFirstPage()
          setSearch(e.target.value)
        }}
      />
      <Tabs
        activeKey={activeTab}
        onChange={({ activeKey }) => {
          setActiveTab(activeKey as TabKeys)
          if (activeKey === 'meetings') {
            window.location.href = stringifyUrl({
              url: Paths.LEGACY_EVENT_MEETINGS.path.replace(
                ':eventName',
                location.attributes.systemName
              ),
              query: { current_location_uuid: location.id },
            })
          }
        }}
        activateOnFocus={false}
        overrides={tabsOverrides}
      >
        <Tab
          key={TabKeys.MEETINGS}
          title={<TabTitle>{formatMessage(tabTitles.meetings)}</TabTitle>}
          overrides={getTabOverrides(tabOverrides)}
        >
          {/* Placeholder spinner until the browser redirects to legacy meeting list page */}
          <ModalSpinner />
        </Tab>
        <Tab
          key={TabKeys.LINKS}
          title={<TabTitle>{formatMessage(tabTitles.links)}</TabTitle>}
          overrides={getTabOverrides(tabOverrides)}
        >
          <SubHeaderRoot>
            <Filters onChange={resetToFirstPage} />
            <StatefulTooltip
              content={formatMessage(messages.labelExportLinks)}
              showArrow
              placement={PLACEMENT.bottom}
            >
              <IconButton
                type="button"
                aria-label={formatMessage(aria.labelExportLinks)}
                size={SIZE.large}
                onClick={() => {
                  const { page, ...restQuery } = linkListQueryParams

                  window.open(
                    stringifyUrl(
                      {
                        url: `${window.location.origin}/${eventName}/exporter/UserInboundLink`,
                        query: {
                          'page[number]': page?.number,
                          'page[size]': page?.size,
                          modName: 'UserInboundLinks::UserInboundLink',
                          ...restQuery,
                        },
                      },
                      { skipEmptyString: true }
                    ),
                    '_blank'
                  )
                }}
              >
                <Export />
              </IconButton>
            </StatefulTooltip>
          </SubHeaderRoot>
          <List
            gridApi={gridApi}
            setGridApi={setGridApi}
            onSort={resetToFirstPage}
            {...listPreferences}
            {...{ data: data?.data, refetch: fetchList }}
          />
          <Footer>
            {total > size && (
              <Pagination
                kind={KIND.light}
                totalItems={total}
                fixedPageSize={PAGE_SIZE}
                currentPageSize={PAGE_SIZE}
                currentPageNumber={pageNumber}
                onPageNumberChange={setPageNumber}
              />
            )}
          </Footer>
        </Tab>
      </Tabs>
    </>
  )
}
