import { VFC } from 'react'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { Config, FORM_ERROR } from 'final-form'
import { Form } from 'react-final-form'
import { useMutation } from 'react-query'
import createDecorator from 'final-form-calculate'
import {
  cancelInboundLinkMutationFn,
  deleteInboundLinkMutationFn,
  useSummitV1Client,
} from 'rest'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import {
  Modal,
  ModalFooter,
  ModalHeader,
  SIZE,
} from '@jifflenow/brightui/modal'
import { KIND } from '@jifflenow/brightui/button'
import { Field, Validation } from 'ui-components/Field'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import { MinWidthButton } from 'ui-components/common/styled-components'
import { Note, StyledModalBody } from './styled-components'
import { FormFieldProps, FormValues } from './types'
import { successButtonOverrides } from './overrides'
import { CancellationReasonField } from './CancellationReasonField'

const messages = defineMessages({
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  cancelConfirmation: {
    defaultMessage: 'Are you sure you want to cancel the link?',
    description: 'Modal heading',
  },
  deleteConfirmation: {
    defaultMessage: 'Are you sure you want to delete the link?',
    description: 'Modal heading',
  },
  cancelScheduled: {
    defaultMessage:
      'Your link will be canceled and already scheduled upcoming meetings will also be canceled',
    description: 'Modal heading',
  },
  deleteScheduled: {
    defaultMessage:
      'Your link will be deleted and already scheduled upcoming meetings will also be canceled',
    description: 'Modal heading',
  },
  cancelLinkSuccessMessage: {
    defaultMessage: 'The link is canceled successfully.',
    description: 'Success message',
  },
  deleteLinkSuccessMessage: {
    defaultMessage: 'The link is deleted successfully.',
    description: 'Success message',
  },
  optionOtherReason: {
    defaultMessage: 'Other reasons',
    description: 'Option label',
  },
  enabledCancelButtonLabel: {
    defaultMessage: 'Discard',
    description: 'Discard',
  },
  enabledSuccessButtonLabel: {
    defaultMessage: 'Proceed',
    description: 'Button label',
  },
  successButtonLabel: {
    defaultMessage: 'Yes',
    description: 'Button label',
  },
  cancelButtonLabel: {
    defaultMessage: 'No',
    description: 'Button label',
  },
  cancellationLabel: {
    defaultMessage: 'Please mention the reason for cancellation',
    description: 'Field label',
  },
  deletionLabel: {
    defaultMessage: 'Please mention the reason for deletion',
    description: 'Field label',
  },
})

const nameDecorator = createDecorator({
  field: 'reason',
  updates: {
    other: () => '',
  },
})

export const FormFields: VFC<FormFieldProps> = ({
  eventName,
  status,
  token,
  action,
  reasons,
  enabled,
  onClose,
  onSuccess,
}) => {
  const { formatMessage } = useIntl()

  const client = useSummitV1Client(eventName)

  const {
    mutateAsync: mutateCancelLink,
    isLoading: cancelInProgress,
  } = useMutation('cancelLink', cancelInboundLinkMutationFn({ token, client }))

  const {
    mutateAsync: mutateDeleteLink,
    isLoading: declineInProgress,
  } = useMutation('declineLink', deleteInboundLinkMutationFn({ token, client }))

  const handleSubmit: Config<FormValues>['onSubmit'] = async ({
    reason,
    other,
  }) => {
    const cancel_comments = other ? other : reason
    try {
      let result
      if (action === 'canCancel') {
        result = await mutateCancelLink({ cancel_comments })
        showSuccessToast(formatMessage(messages.cancelLinkSuccessMessage))
      }
      if (action === 'canDelete') {
        result = await mutateDeleteLink({ cancel_comments })
        showSuccessToast(formatMessage(messages.deleteLinkSuccessMessage))
      }
      onSuccess(action, result)
      return undefined
    } catch (error) {
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  const options = [
    ...reasons.map(reason => ({ label: reason, id: reason })),
    { label: formatMessage(messages.optionOtherReason), id: 'other' },
  ]

  return (
    <>
      {(declineInProgress || cancelInProgress) && <ModalSpinner />}
      <Form<FormValues>
        // TODO: Awaiting fix for this [bug](https://github.com/final-form/final-form-calculate/issues/38)
        // to remove `ts-expect-error`.
        // @ts-expect-error
        decorators={[nameDecorator]}
        onSubmit={handleSubmit}
        render={({ handleSubmit }) => (
          <Modal size={SIZE.small} onClose={onClose} isOpen>
            <form onSubmit={handleSubmit}>
              <ModalHeader>
                {formatMessage(
                  messages[
                    action === 'canCancel'
                      ? status === 'scheduled'
                        ? 'cancelScheduled'
                        : 'cancelConfirmation'
                      : status === 'scheduled'
                      ? 'deleteScheduled'
                      : 'deleteConfirmation'
                  ]
                )}
              </ModalHeader>
              <StyledModalBody>
                {enabled && status === 'scheduled' && (
                  <Field
                    label={formatMessage(
                      messages[
                        action === 'canCancel'
                          ? 'cancellationLabel'
                          : 'deletionLabel'
                      ]
                    )}
                    name="reason"
                    render={CancellationReasonField}
                    options={options}
                    validations={[Validation.REQUIRED]}
                  />
                )}
                <Note>
                  <FormattedMessage
                    defaultMessage="Please note: This action cannot be reverted."
                    description="Help text"
                  />
                </Note>
              </StyledModalBody>
              <ModalFooter>
                <MinWidthButton
                  type="button"
                  kind={KIND.tertiary}
                  onClick={onClose}
                  data-pendo="shared-link/discard-cancel-reasons"
                >
                  {formatMessage(
                    messages[
                      enabled && status === 'scheduled'
                        ? 'enabledCancelButtonLabel'
                        : 'cancelButtonLabel'
                    ]
                  )}
                </MinWidthButton>
                <MinWidthButton
                  type="submit"
                  overrides={successButtonOverrides}
                  data-pendo="shared-link/submit-cancel-reasons"
                >
                  {formatMessage(
                    messages[
                      enabled && status === 'scheduled'
                        ? 'enabledSuccessButtonLabel'
                        : 'successButtonLabel'
                    ]
                  )}
                </MinWidthButton>
              </ModalFooter>
            </form>
          </Modal>
        )}
      />
    </>
  )
}
