import { FC, useState } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useMutation } from 'react-query'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { ICellRendererParams } from '@ag-grid-community/core'
import { PLACEMENT } from '@jifflenow/brightui/popover'
import { StatefulMenu, OnItemSelect } from '@jifflenow/brightui/menu'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { renewInboundLinkMutationFn, useSummitV1Client } from 'rest'
import { showSuccessToast, showErrorToast } from 'utils/toast'
import { useEventLocation } from 'providers/LocationProvider'
import { InlineMenuOverrides } from 'components/ag-grid'
import { ThreeDotsPopover } from 'ui-components/ThreeDotsPopover'
import { Remind } from 'components/SharedLinks/common/Remind'
import {
  CancelConfirmation,
  ConfirmationActions,
  LinkInfo,
  SuccessCallback,
} from '../../common'
import { scheduleMeeting } from '../../scheduleMeeting'
import { InlineActionContentProps, ActionItem, SharedLink } from './types'

const messages = defineMessages({
  schedule: {
    defaultMessage: 'Schedule',
    description: 'Grid row action label',
  },
  renew: {
    defaultMessage: 'Renew',
    description: 'Grid row action label',
  },
  remind: {
    defaultMessage: 'Remind',
    description: 'Grid row action label',
  },
  labelCancel: {
    defaultMessage: 'Cancel',
    description: 'Grid row action label',
  },
  labelDelete: {
    defaultMessage: 'Delete',
    description: 'Grid row action label',
  },
  copy: {
    defaultMessage: 'Copy link',
    description: 'Grid row action label',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  copied: {
    defaultMessage: 'Link copied to clipboard',
    description: 'Grid row action label',
  },
  linkRenewSuccessMessage: {
    defaultMessage: 'The link is renewed successfully.',
    description: 'Grid row action label',
  },
})

const aria = defineMessages({
  labelEngagementMore: {
    defaultMessage: 'Shared link list more option',
    description: 'Inline actions',
  },
})

const onItemSelect: OnItemSelect = args => args.item.onItemSelect?.(args)

const Content: FC<InlineActionContentProps> = props => {
  const { formatMessage } = useIntl()
  const [showRemind, setShowRemind] = useState(false)
  const [action, setAction] = useState<ConfirmationActions>()

  const { value, data, onClose, node, api } = props
  const {
    id: token,
    status,
    relationships: {
      attendees,
      engagementType: { id: engagementTypeUuid },
    },
  } = data

  const {
    id: currentLocationUuid,
    attributes: { systemName },
  } = useEventLocation().location!

  const summitClient = useSummitV1Client(systemName)

  const externalAttendees = attendees.filter(({ type }) => type === 'external')

  const { mutate: renewLink, isLoading: renewLinkInProgress } = useMutation(
    'renewLink',
    renewInboundLinkMutationFn,
    {
      onSuccess: ({ status, expiresAt }) => {
        node.setData({ ...data, status, expiresAt })
        showSuccessToast(formatMessage(messages.linkRenewSuccessMessage))
      },
      onError: () => {
        showErrorToast(formatMessage(messages.genericError))
      },
    }
  )

  const menu: ActionItem[] = [
    value.canScheduleMeeting && {
      label: formatMessage(messages.schedule),
      onItemSelect: () => scheduleMeeting(data),
    },
    value.canCopy && {
      label: (
        <CopyToClipboard
          text={data.link}
          onCopy={() => {
            showSuccessToast(formatMessage(messages.copied))
            onClose()
          }}
        >
          <span>{formatMessage(messages.copy)}</span>
        </CopyToClipboard>
      ),
    },
    externalAttendees.length &&
      value.canRemindParticipants && {
        label: formatMessage(messages.remind),
        onItemSelect: () => setShowRemind(true),
      },
    value.canRenewExpiry && {
      label: formatMessage(messages.renew),
      onItemSelect: () => renewLink({ client: summitClient, token }),
    },
    value.canCancel && {
      label: formatMessage(messages.labelCancel),
      onItemSelect: () => setAction('canCancel'),
    },
    value.canDelete && {
      label: formatMessage(messages.labelDelete),
      onItemSelect: () => setAction('canDelete'),
    },
  ].filter(Boolean) as ActionItem[]

  const handleSuccess: SuccessCallback<SharedLink> = (
    action: ConfirmationActions,
    result
  ) => {
    if (action === 'canCancel') {
      node.setDataValue('status', result?.status)
    }
    if (action === 'canDelete') {
      api.applyTransaction({ remove: [node.data] })
    }
    onClose()
  }

  const linkInfo: LinkInfo = {
    status,
    token,
    eventName: systemName,
    currentLocationUuid,
    engagementTypeUuid,
  }

  const showLoader = renewLinkInProgress

  return (
    <>
      {showLoader && <ModalSpinner />}
      {showRemind && (
        <Remind
          token={token}
          eventName={systemName}
          attendees={externalAttendees}
          onClose={() => setShowRemind(false)}
        />
      )}
      {action && (
        <CancelConfirmation
          onClose={() => setAction(undefined)}
          onSuccess={handleSuccess}
          action={action}
          {...linkInfo}
        />
      )}
      <StatefulMenu
        items={menu}
        onItemSelect={onItemSelect}
        overrides={InlineMenuOverrides}
      />
    </>
  )
}

export const InlineActions: FC<ICellRendererParams> = props => {
  const { formatMessage } = useIntl()

  return (
    <ThreeDotsPopover
      popoverMargin={8}
      placement={PLACEMENT.bottomRight}
      content={({ close }) => <Content {...props} onClose={close} />}
      ariaLabel={formatMessage(aria.labelEngagementMore)}
    />
  )
}
