import {
  useContext,
  FC,
  useMemo,
  useState,
  useEffect,
  createContext,
} from 'react'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import merge from 'deepmerge'
import {
  IfRejected,
  useSharedLinkPreferences,
  patchSharedLinkPreferences as patch,
} from 'rest'
import { ErrorPage } from 'ui-components/ErrorPage'
import { components } from 'types/openapi'
import {
  FilterPreferences,
  FilterPreferencesResponse,
  PatchPreferencesType,
  Preferences,
  SortByPreferencesType,
} from './types'

type PatchPreferences = components['schemas']['PatchListFilterPreferences']
type ContextT = {
  preferences: Preferences
  patchPreferences: (preferences: Partial<Preferences>) => void
}
type GeneralNameAndUuidFormat = components['schemas']['GeneralNameAndUuidFormat']

const getUuids = ({ uuid }: GeneralNameAndUuidFormat) => uuid
const getPatchValues = (values: Preferences): PatchPreferences => ({
  data: {
    sharedLinks: {
      ...values,
      participants: values.participants.map(getUuids),
      engagementTypes: values.engagementTypes.map(getUuids),
    },
  },
})

const INITIAL_STATE: Preferences = {
  sortBy: 'expiry',
  statuses: ['pending', 'expired'],
  linkTypes: ['singleUse'],
  engagementTypes: [],
  participants: [],
}
const Context = createContext<ContextT | null>(null)
const SHARED_LINKS_PREFERENCES = 'sharedLinksPreferences'

const Provider: FC<ContextT> = ({
  children,
  preferences,
  patchPreferences,
}) => {
  const value = useMemo<ContextT>(() => ({ preferences, patchPreferences }), [
    preferences,
    patchPreferences,
  ])

  return <Context.Provider value={value}>{children}</Context.Provider>
}

const usePreferences = () => {
  const [preferences, setPreferences] = useState<Preferences>({} as Preferences)

  useEffect(() => {
    const handleLocalStorageChange = (e: StorageEvent) => {
      if (e.key === SHARED_LINKS_PREFERENCES) {
        setPreferences(JSON.parse(e.newValue || '{}'))
      }
    }
    window.addEventListener('storage', handleLocalStorageChange)
    return () => {
      window.removeEventListener('storage', handleLocalStorageChange)
    }
  }, [setPreferences])

  const initPreferences = (data: FilterPreferencesResponse) => {
    const preferences = data?.data?.sharedLinks ?? {}
    const initialPreferences = Object.keys(INITIAL_STATE).reduce<Preferences>(
      (acc, key) => ({
        ...acc,
        [key]:
          preferences[key as keyof FilterPreferences['sharedLinks']] ??
          INITIAL_STATE[key as keyof Preferences],
      }),
      INITIAL_STATE
    )
    setPreferences(initialPreferences)
    window.localStorage.setItem(
      SHARED_LINKS_PREFERENCES,
      JSON.stringify(initialPreferences)
    )
  }

  const patchPreferences = (payload: PatchPreferencesType) => {
    const { sortBy, sortOrder } = payload
    if (sortBy) {
      const isSortByExpiresAt =
        sortBy === ('expiresAt' as SortByPreferencesType)
      payload.sortBy =
        isSortByExpiresAt && sortOrder === 'ASC'
          ? 'expiry'
          : isSortByExpiresAt && sortOrder === 'DESC'
          ? '-expiry'
          : sortBy === 'createdAt' && sortOrder === 'DESC'
          ? `-${sortBy}`
          : sortBy
    }
    const pref = merge(preferences, payload, { arrayMerge: (_, s) => s })
    setPreferences(pref)
    patch(getPatchValues(pref)).then(() =>
      window.localStorage.setItem(
        SHARED_LINKS_PREFERENCES,
        JSON.stringify(pref)
      )
    )
  }

  return { preferences, initPreferences, patchPreferences }
}

const SharedLinkPreferencesFetcher: FC<{
  onResolve: (data: FilterPreferencesResponse) => void
}> = props => {
  const state = useSharedLinkPreferences(props)

  return (
    <>
      {!state.isSettled && <ModalSpinner />}
      <IfRejected state={state}>
        {() => <ErrorPage onButtonClick={state.reload} />}
      </IfRejected>
    </>
  )
}

export const SharedLinkPreferencesProvider: FC = ({ children }) => {
  const { preferences, initPreferences, patchPreferences } = usePreferences()

  return Object.keys(preferences).length === 0 ? (
    <SharedLinkPreferencesFetcher onResolve={initPreferences} />
  ) : (
    <Provider preferences={preferences} patchPreferences={patchPreferences}>
      {children}
    </Provider>
  )
}

export const useSharedLinksPreferences = () => {
  const context = useContext(Context)!
  if (context === undefined && process.env.NODE_ENV !== 'production') {
    throw new Error(
      'useSharedLinksPreferences must be used within a SharedLinkPreferencesProvider'
    )
  }

  return context
}
