import { useMemo, VFC } from 'react'
import { Config, FORM_ERROR } from 'final-form'
import { Form } from 'react-final-form'
import { defineMessages, FormattedMessage, useIntl } from 'react-intl'
import { useMutation } from 'react-query'
import { KIND } from '@jifflenow/brightui/button'
import {
  Modal,
  ModalFooter,
  ModalHeader,
  ModalBody,
  SIZE,
} from '@jifflenow/brightui/modal'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { Select, Option, Value } from '@jifflenow/brightui/select'
import { FormControl } from '@jifflenow/brightui/form-control'
import { showSuccessToast, showErrorToast } from 'utils/toast'
import { Field, Validation } from 'ui-components/Field'
import { remindParticipantsMutationFn, useSummitV1Client } from 'rest'
import { RemindProps } from './types'
import { FooterButton } from './styled-components'
import { successButtonOverrides } from './overrides'

const messages = defineMessages({
  labelAll: {
    defaultMessage: 'All',
    description: 'Dropdown option',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  successMessage: {
    defaultMessage: 'Participants are reminded successfully.',
    description: 'Success message',
  },
})

export const Remind: VFC<RemindProps> = ({
  token,
  eventName,
  attendees,
  onClose,
}) => {
  const { formatMessage } = useIntl()

  const summitClient = useSummitV1Client(eventName)

  const { mutateAsync, isLoading } = useMutation(
    'remindParticipants',
    remindParticipantsMutationFn({ token, client: summitClient })
  )

  const handleSubmit: Config<{
    participants: string[]
  }>['onSubmit'] = async values => {
    try {
      const { participants } = values
      await mutateAsync({
        userUuids: participants.some(id => id === 'All')
          ? attendees.map(({ uuid }) => uuid!)
          : participants,
      })
      onClose()
      showSuccessToast(formatMessage(messages.successMessage))
      return undefined
    } catch (error) {
      showErrorToast(formatMessage(messages.genericError))
      return { [FORM_ERROR]: { code: 'GenericError' } }
    }
  }

  const dropdownOptions = [
    ...attendees.map(
      ({ uuid: id, email: label }): Option => ({
        label,
        id,
      })
    ),
    attendees.length && {
      label: formatMessage(messages.labelAll),
      id: 'All',
    },
  ].filter(Boolean) as Option[]

  const participantIds: string[] = useMemo(
    () => attendees.map(({ uuid }) => uuid!),
    [attendees]
  )

  return (
    <div onClick={e => e.stopPropagation()}>
      {isLoading && <ModalSpinner />}
      <Modal isOpen size={SIZE.small} onClose={onClose}>
        <Form
          onSubmit={handleSubmit}
          initialValues={{ participants: participantIds }}
          render={({ handleSubmit }) => (
            <form onSubmit={handleSubmit} noValidate>
              <ModalHeader>
                <FormattedMessage
                  defaultMessage="Send a reminder to following participants"
                  description="Dialog header"
                />
              </ModalHeader>
              <ModalBody>
                <Field<(string | number | undefined)[] | undefined>
                  name="participants"
                  format={(value: Option['id'][] | undefined) =>
                    value
                      ? dropdownOptions.filter(
                          option => value.indexOf(option.id) + 1
                        )
                      : []
                  }
                  parse={(value: Option[]) =>
                    value.length
                      ? value.some(({ id }) => id === 'All')
                        ? ['All']
                        : value.map((option: Option) => option.id)
                      : undefined
                  }
                  render={({ input, meta: { touched, error } }) => (
                    <FormControl error={touched && error}>
                      <Select
                        {...input}
                        multi
                        id="managedUsers"
                        options={dropdownOptions}
                        backspaceClearsInputValue
                        onChange={({ value }: { value: Value[] }) =>
                          input.onChange(value)
                        }
                        required
                      />
                    </FormControl>
                  )}
                  validations={[Validation.REQUIRED]}
                />
              </ModalBody>
              <ModalFooter>
                <FooterButton kind={KIND.tertiary} onClick={onClose}>
                  <FormattedMessage
                    defaultMessage="Cancel"
                    description="Button label"
                  />
                </FooterButton>
                <FooterButton type="submit" overrides={successButtonOverrides}>
                  <FormattedMessage
                    defaultMessage="Send"
                    description="Button label"
                  />
                </FooterButton>
              </ModalFooter>
            </form>
          )}
        />
      </Modal>
    </div>
  )
}
