export const LinkStatus = styled<{ $status: Status }, 'div'>(
  'div',
  ({ $status, $theme: { colors, sizing } }) => {
    const statusColor: Record<Status, string> = {
      pending: colors.warning,
      scheduled: colors.positive,
      cancelled: colors.negative,
      expired: colors.negative,
    }

    return {
      backgroundColor: statusColor[$status],
      position: 'absolute',
      left: 0,
      right: 0,
      bottom: 0,
      height: sizing.scale100,
    }
  }
)

