import { useEffect, useState, VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { showErrorToast } from 'utils/toast'
import { useEngagementTypeSettingsByUuid } from 'gql'
import { getEngagementTypeSettingsByUuid_getEngagementTypeSettingsByUuid_value_CustomNameValue as CustomNameValue } from 'gql/types/getEngagementTypeSettingsByUuid'
import {
  CancelOptions,
  CancelOptionsResponse,
  CancelConfirmationProps,
} from './types'
import { FormFields } from './FormFields'

const messages = defineMessages({
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
})

export const CancelConfirmation: VFC<CancelConfirmationProps> = props => {
  const { formatMessage } = useIntl()

  const { engagementTypeUuid, currentLocationUuid, onClose } = props

  const [cancelOptions, setCancelOptions] = useState<
    CancelOptions | undefined
  >()

  const { loading, engagementTypeSettings } = useEngagementTypeSettingsByUuid({
    variables: {
      engagementTypeUuid,
      names: ['cancel_comments_enabled', 'predefined_cancel_comments'],
    },
    context: {
      headers: { 'X-Location-Uuid': currentLocationUuid },
    },
    onError: () => {
      onClose()
      showErrorToast(formatMessage(messages.genericError))
    },
  })

  useEffect(() => {
    if (engagementTypeSettings) {
      const values = engagementTypeSettings.reduce(
        (acc, { name, value }) => ({
          ...acc,
          [name]: (value as CustomNameValue).value,
        }),
        {}
      )

      setCancelOptions({
        cancel_comments_enabled: (values as CancelOptionsResponse)
          .cancel_comments_enabled,
        predefined_cancel_comments: JSON.parse(
          (values as CancelOptionsResponse).predefined_cancel_comments
        ),
      })
    }
  }, [engagementTypeSettings])

  if (loading || !cancelOptions) {
    return <ModalSpinner />
  }

  return (
    <FormFields
      {...props}
      enabled={cancelOptions.cancel_comments_enabled === 'true'}
      reasons={cancelOptions.predefined_cancel_comments}
    />
  )
}
