import { VFC, useState } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useMutation, useQueryClient } from 'react-query'
import { CopyToClipboard } from 'react-copy-to-clipboard'
import { StatefulMenu, OnItemSelect } from '@jifflenow/brightui/menu'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { showSuccessToast, showErrorToast } from 'utils/toast'
import { renewInboundLinkMutationFn, useSummitV1Client } from 'rest'
import { components } from 'types/openapi'
import { menuOverrides } from 'ui-components/Menu/overrides'
import { ThreeDotsPopover } from 'ui-components/ThreeDotsPopover'
import { CancelConfirmation, LinkInfo, Remind } from '../common'
import {
  ConfirmationActions,
  LinkActionsMenuProps,
  OptionsMenuProps,
  SharedLinkMenuItem,
} from './types'

type SharedLink = components['schemas']['SharedLink']
type LinkActions = SharedLink['relationships']['actions']
type SuccessMessageActions = Extract<
  keyof LinkActions,
  'canRenewExpiry' | 'canCancel' | 'canDelete'
>

const messages = defineMessages({
  editLink: {
    defaultMessage: 'Edit',
    description: 'Menu option',
  },
  scheduleLink: {
    defaultMessage: 'Schedule',
    description: 'Menu option',
  },
  copyLink: {
    defaultMessage: 'Copy link',
    description: 'Menu option',
  },
  remindParticipants: {
    defaultMessage: 'Remind',
    description: 'Menu option',
  },
  renewLink: {
    defaultMessage: 'Renew',
    description: 'Menu option',
  },
  cancel: {
    defaultMessage: 'Cancel',
    description: 'Menu option',
  },
  delete: {
    defaultMessage: 'Delete',
    description: 'Menu option',
  },
  linkCopied: {
    defaultMessage: 'Link copied to clipboard',
    description: 'Success message',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  cancelConfirmation: {
    defaultMessage: 'Are you sure you want to cancel the link?',
    description: 'Modal heading',
  },
})

const actionSuccessMessage = defineMessages<SuccessMessageActions>({
  canRenewExpiry: {
    defaultMessage: 'The link is renewed successfully',
    description: 'Success message',
  },
  canCancel: {
    defaultMessage: 'The link is canceled successfully.',
    description: 'Success message',
  },
  canDelete: {
    defaultMessage: 'The link is deleted successfully.',
    description: 'Success message',
  },
})

const onItemSelect: OnItemSelect = args => args.item.onItemSelect?.(args)

const OptionsMenu: VFC<OptionsMenuProps> = ({
  close,
  onLinkEdit,
  onScheduleMeeting,
  ...rest
}) => {
  const { formatMessage } = useIntl()
  const queryClient = useQueryClient()

  const {
    id: token,
    link,
    eventName,
    status,
    actions: {
      canEdit,
      canCopy,
      canRemindParticipants,
      canRenewExpiry,
      canCancel,
      canDelete,
      canScheduleMeeting,
    },
    attendees,
    currentLocationUuid,
    engagementTypeUuid,
  } = rest

  const summitClient = useSummitV1Client(eventName)

  const [action, setAction] = useState<ConfirmationActions>()
  const [showRemind, setShowRemind] = useState(false)

  const handleError = () => {
    showErrorToast(formatMessage(messages.genericError))
    close()
  }

  const handleSuccess = (action: SuccessMessageActions) => {
    showSuccessToast(formatMessage(actionSuccessMessage[action]))
    queryClient.invalidateQueries('sharedLinkDetails')
    queryClient.invalidateQueries('sharedLinks')
    close()
  }

  const params = { client: summitClient, token }

  const { mutate: renewLink, isLoading: renewLinkInProgress } = useMutation(
    'renewLink',
    renewInboundLinkMutationFn,
    {
      onSuccess: () => handleSuccess('canRenewExpiry'),
      onError: handleError,
    }
  )

  const externalAttendees = attendees.filter(({ type }) => type === 'external')

  const menu: SharedLinkMenuItem[] = [
    canEdit && {
      label: formatMessage(messages.editLink),
      dataPendo: 'shared-links/edit-link',
      onItemSelect: () => {
        onLinkEdit?.()
        close()
      },
    },
    canCopy && {
      label: (
        <CopyToClipboard
          text={link}
          onCopy={() => {
            showSuccessToast(formatMessage(messages.linkCopied))
            close()
          }}
        >
          <span>{formatMessage(messages.copyLink)}</span>
        </CopyToClipboard>
      ),
      dataPendo: 'shared-links/copy-link',
    },
    canScheduleMeeting && {
      label: formatMessage(messages.scheduleLink),
      dataPendo: 'shared-links/schedule-link',
      onItemSelect: () => {
        onScheduleMeeting?.()
        close()
      },
    },
    canRemindParticipants &&
      externalAttendees.length && {
        label: formatMessage(messages.remindParticipants),
        onItemSelect: () => setShowRemind(true),
      },
    canRenewExpiry && {
      label: formatMessage(messages.renewLink),
      dataPendo: 'shared-links/renew-link',
      onItemSelect: () => renewLink(params),
    },
    canCancel && {
      label: formatMessage(messages.cancel),
      dataPendo: 'shared-links/cancel-link',
      onItemSelect: () => setAction('canCancel'),
    },
    canDelete && {
      label: formatMessage(messages.delete),
      dataPendo: 'shared-links/delete-link',
      onItemSelect: () => setAction('canDelete'),
    },
  ].filter(Boolean) as SharedLinkMenuItem[]

  const linkActionInProgress = renewLinkInProgress

  const linkInfo: LinkInfo = {
    status,
    token,
    eventName,
    currentLocationUuid,
    engagementTypeUuid,
  }

  return (
    <>
      {action && (
        <CancelConfirmation
          onClose={() => setAction(undefined)}
          onSuccess={handleSuccess}
          action={action}
          {...linkInfo}
        />
      )}
      {showRemind && (
        <Remind
          token={token}
          eventName={eventName}
          attendees={externalAttendees}
          onClose={() => setShowRemind(false)}
        />
      )}
      {linkActionInProgress && <ModalSpinner />}
      <StatefulMenu
        items={menu}
        onItemSelect={onItemSelect}
        overrides={menuOverrides}
      />
    </>
  )
}

export const LinkActionsMenu: VFC<LinkActionsMenuProps> = props => {
  const { actions } = props

  const showActions = Object.keys(actions).some(
    action => actions[action as keyof LinkActions]
  )

  return showActions ? (
    <ThreeDotsPopover
      content={({ close }) => <OptionsMenu close={close} {...props} />}
    />
  ) : null
}
