import { FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { TextFilter } from '@jifflenow/brightui/filter'
import { filterOverrides, FilterItem } from 'components/ag-grid'
import { components } from 'types/openapi'
import { StaticFilterProps } from './types'

type InboundLinkStatus = components['schemas']['InboundLinkStatus']

const labels = defineMessages<InboundLinkStatus>({
  scheduled: {
    defaultMessage: 'Scheduled',
    description: 'Options label',
  },
  expired: {
    defaultMessage: 'Expired',
    description: 'Options label',
  },
  cancelled: {
    defaultMessage: 'Canceled',
    description: 'Options label',
  },
  pending: {
    defaultMessage: 'Pending',
    description: 'Options label',
  },
})

const filterValues: InboundLinkStatus[] = [
  'pending',
  'expired',
  'scheduled',
  'cancelled',
]

export const StatusFilter: FC<StaticFilterProps> = ({
  onChange,
  value,
  label,
}) => {
  const { formatMessage } = useIntl()

  const strOptionMapper = (text: string) => ({
    id: text,
    label: formatMessage(labels[text as keyof typeof labels]),
  })

  return (
    <FilterItem>
      <TextFilter
        label={label}
        onChange={onChange}
        value={value.map(strOptionMapper)}
        options={filterValues.map(strOptionMapper)}
        overrides={filterOverrides}
      />
    </FilterItem>
  )
}
