import { VFC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { useMutation, useQueryClient } from 'react-query'
import { StatefulMenu, OnItemSelect } from '@jifflenow/brightui/menu'
import { PersonOutlined, Person } from '@jifflenow/brightui/icon'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { AutoTooltip } from 'ui-components/AutoTooltip'
import { menuOverrides } from 'ui-components/Menu/overrides'
import { ThreeDotsPopover } from 'ui-components/ThreeDotsPopover'
import {
  remindParticipantsMutationFn,
  removeParticipantsMutationFn,
  useSummitV1Client,
} from 'rest'
import { showErrorToast, showSuccessToast } from 'utils/toast'
import {
  ParticipantEmail,
  ParticipantsList,
  ParticipantsListItem,
  ParticipantDetails,
  ParticipantActions,
  TagInfo,
  Tag,
} from './styled-components'
import { optionalIconOverride } from './overrides'
import {
  ParticipantsAction,
  ParticipantActionsMenuProps,
  ParticipantsProps,
  ParticipantsOptionsMenuProps,
  SharedLinkMenuItem,
} from './types'

const messages = defineMessages({
  remindSuccessMessage: {
    defaultMessage: 'Reminded participant successfully',
    description: 'Participant remind success message',
  },
  removeSuccessMessage: {
    defaultMessage: 'Removed participant successfully',
    description: 'Participant remove success message',
  },
  remindLabel: {
    defaultMessage: 'Remind',
    description: 'Menu option',
  },
  removeLabel: {
    defaultMessage: 'Remove',
    description: 'Menu option',
  },
  genericError: {
    defaultMessage: 'Something is not right. Please retry.',
    description: 'Error message',
  },
  meetingHostLabel: {
    defaultMessage: 'Meeting Host',
    description: 'Label',
  },
})

const onItemSelect: OnItemSelect = args => args.item.onItemSelect?.(args)

const ParticipantsOptionsMenu: VFC<ParticipantsOptionsMenuProps> = ({
  close,
  ...rest
}) => {
  const { formatMessage } = useIntl()
  const queryClient = useQueryClient()

  const {
    id: token,
    eventName,
    actions: { canRemindParticipants, canEdit },
    userUuid,
  } = rest

  const client = useSummitV1Client(eventName)

  const handleError = () => {
    showErrorToast(formatMessage(messages.genericError))
    close()
  }

  const handleSuccess = (action: keyof ParticipantsAction) => {
    switch (action) {
      case 'canRemindParticipants':
        showSuccessToast(formatMessage(messages.remindSuccessMessage))
        break
      case 'canEdit':
        showSuccessToast(formatMessage(messages.removeSuccessMessage))
        break
    }

    queryClient.invalidateQueries('sharedLinkDetails')
    close()
  }

  const params = { client, token }

  const {
    mutate: remindParticipants,
    isLoading: remindInProgress,
  } = useMutation('remindParticipants', remindParticipantsMutationFn(params), {
    onSuccess: () => handleSuccess('canRemindParticipants'),
    onError: handleError,
  })

  const {
    mutate: removeParticipants,
    isLoading: removeInProgress,
  } = useMutation('remindParticipants', removeParticipantsMutationFn, {
    onSuccess: () => handleSuccess('canEdit'),
    onError: handleError,
  })

  const linkActionInProgress = remindInProgress || removeInProgress

  const menu: SharedLinkMenuItem[] = ([
    canRemindParticipants && {
      label: formatMessage(messages.remindLabel),
      dataPendo: 'shared-links/remind-participants',
      onItemSelect: () => remindParticipants({ userUuids: [userUuid] }),
    },
    canEdit && {
      label: formatMessage(messages.removeLabel),
      dataPendo: 'shared-links/remove-participant',
      onItemSelect: () => removeParticipants({ ...params, uuid: userUuid }),
    },
  ].filter(Boolean) as unknown) as SharedLinkMenuItem[]

  return (
    <>
      {linkActionInProgress && <ModalSpinner />}
      <StatefulMenu
        items={menu}
        onItemSelect={onItemSelect}
        overrides={menuOverrides}
      />
    </>
  )
}

const ParticipantActionsMenu: VFC<ParticipantActionsMenuProps> = props => {
  const { actions } = props

  const showActions = Object.keys(actions).some(
    action => actions[action as keyof ParticipantsAction]
  )

  return showActions ? (
    <ThreeDotsPopover
      content={({ close }) => (
        <ParticipantsOptionsMenu close={close} {...props} />
      )}
    />
  ) : null
}

export const Participants: VFC<ParticipantsProps> = ({ linkDetails }) => {
  const { formatMessage } = useIntl()

  const {
    id,
    attributes: {
      link,
      relationships: {
        location: { systemName: eventName },
        actions: { canRemindParticipants, canEdit },
        attendees,
      },
    },
  } = linkDetails

  return (
    <ParticipantsList>
      {attendees?.map(({ email, uuid, optional, meetingHost }) => {
        const actionsMenuProps: ParticipantActionsMenuProps = {
          id,
          link,
          actions: {
            canRemindParticipants,
            canEdit,
          },
          eventName,
          userUuid: uuid!,
        }

        return (
          <ParticipantsListItem key={email}>
            <ParticipantDetails>
              <AutoTooltip content={email} showArrow>
                <ParticipantEmail>{email}</ParticipantEmail>
              </AutoTooltip>
              <TagInfo>
                {meetingHost && (
                  <Tag>{formatMessage(messages.meetingHostLabel)}</Tag>
                )}
              </TagInfo>
            </ParticipantDetails>
            <ParticipantActions>
              {optional ? (
                <PersonOutlined overrides={optionalIconOverride} />
              ) : (
                <Person overrides={optionalIconOverride} />
              )}
              <ParticipantActionsMenu {...actionsMenuProps} />
            </ParticipantActions>
          </ParticipantsListItem>
        )
      })}
    </ParticipantsList>
  )
}
