import { FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Option } from '@jifflenow/brightui/filter'
import { FiltersContainer, DataOptions, Options } from 'components/ag-grid'
import { useSharedLinksPreferences } from '../SharedLinkPreferencesProvider'
import { StatusFilter } from './StatusFilter'
import { TypeFilter } from './TypeFilter'
import { ParticipantsFilter } from './ParticipantsFilter'
import { EngagementTypesFilter } from './EngagementTypesFilter'
// import { CreatedByFilter } from './CreatedByFilter'
import { FiltersProps } from './types'

const messages = defineMessages({
  status: {
    defaultMessage: 'Status',
    description: 'Filter label',
  },
  type: {
    defaultMessage: 'Link type',
    description: 'Filter label',
  },
  participants: {
    defaultMessage: 'Participants',
    description: 'Filter label',
  },

  engagementTypes: {
    defaultMessage: 'Meeting types',
    description: 'Filter option label',
  },
  createdBy: {
    defaultMessage: 'Created By',
    description: 'Filter option label',
  },
})

const optionMapper = (o: DataOptions) => ({ id: o.uuid, label: o.name })
const idMapper = (option: Option) => option.id

export const Filters: FC<FiltersProps> = ({ onChange }) => {
  const { formatMessage } = useIntl()
  const { patchPreferences, preferences } = useSharedLinksPreferences()
  const {
    statuses = [],
    linkTypes = [],
    participants = [],
    engagementTypes = [],
  } = preferences

  return (
    <FiltersContainer>
      <StatusFilter
        value={statuses}
        label={formatMessage(messages.status)}
        onChange={(statuses: Option) => {
          onChange()
          patchPreferences({
            statuses: statuses.map(idMapper),
          })
        }}
      />
      <TypeFilter
        value={linkTypes}
        label={formatMessage(messages.type)}
        onChange={(types: Option) => {
          onChange()
          patchPreferences({
            linkTypes: types.map(idMapper),
          })
        }}
      />
      <ParticipantsFilter
        label={formatMessage(messages.participants)}
        value={participants.map(optionMapper)}
        onChange={(users: Option) => {
          onChange()
          patchPreferences({
            participants: users.map(({ id, label }: Options) => ({
              uuid: id,
              name: label,
            })),
          })
        }}
      />
      <EngagementTypesFilter
        label={formatMessage(messages.engagementTypes)}
        value={engagementTypes.map(optionMapper)}
        onChange={(engagementTypes: Option) => {
          onChange()
          patchPreferences({
            engagementTypes: engagementTypes?.map(({ id, label }: Options) => ({
              uuid: id,
              name: label,
            })),
          })
        }}
      />
      {/* TODO: Enable the filters once api is done */}
      {/* <CreatedByFilter
        label={formatMessage(messages.createdBy)}
        value={engagementTypes.map(optionMapper)}
        onChange={(users: Option) => {
          onChange()
          patchPreferences({
            participants: users.map(({ id, label }: Options) => ({
              uuid: id,
              name: label,
            })),
          })
        }}
      /> */}
    </FiltersContainer>
  )
}
