import { FC, VFC } from 'react'
import { useQuery } from 'react-query'
import { defineMessages, useIntl } from 'react-intl'
import { components } from 'types/openapi'
import {
  LocationOutlined,
  MeetingsOutlined,
  Message,
  TopicOutlined,
  VideoOutlined,
} from '@jifflenow/brightui/icon'
import { DrawerPane } from 'ui-components/DrawerPane'
import { ErrorPage } from 'ui-components/ErrorPage'
import { getSharedLinkDetailsQueryFn, useSummitV1Client } from 'rest'
import { Loader } from './Loader'
import { Header } from './Header'
import {
  LinkDetailsDrawerProps,
  LinkDetailsProps,
  LinkDetailsViewProps,
  LinkInfoProps,
} from './types'
import {
  InfoContent,
  InfoDescription,
  InfoIconRoot,
  InfoLabel,
  InfoRoot,
  LinkDetailsContent,
  LinkDetailsRoot,
} from './styled-components'
import { Tabs } from './Tabs'
import { AvailableSlots } from './AvailableSlots'
import { FooterAction } from './FooterAction'
import { LinkStatus } from './LinkStatus'
import { LinkIcon } from './icons'
import { linkInfoIconOverride } from './overrides'

type LocationType = components['schemas']['LocationType']

const messages = defineMessages({
  requestorLabel: {
    defaultMessage: 'Requestor',
    description: 'Requestor placeholder',
  },
  preferredLabel: {
    defaultMessage: 'Preferred',
    description: 'Preferred placeholder',
  },
  slotsLabel: {
    defaultMessage: '{count} slots',
    description: 'Available slots label',
  },
  meetingsQuotaLabel: {
    defaultMessage: '{meetingsQuota} Use',
    description: 'Meetings Quota placeholder',
  },
  unlimitedUseLabel: {
    defaultMessage: 'Unlimited Use (999)',
    description: 'Unlimited use placeholder',
  },
})

const roomLabels = defineMessages<Exclude<LocationType, 'offsite'>>({
  room: {
    defaultMessage: 'Room',
    description: 'Room placeholder',
  },
  boothTour: {
    defaultMessage: 'Designated Location',
    description: 'Booth tour placeholder',
  },
})

export const LinkInfo: FC<LinkInfoProps> = ({ Icon, children }) => (
  <InfoRoot>
    <InfoIconRoot>
      <Icon size={16} overrides={linkInfoIconOverride} />
    </InfoIconRoot>
    <InfoContent>{children}</InfoContent>
  </InfoRoot>
)

const LinkDetailsView: VFC<LinkDetailsViewProps> = ({
  linkDetails,
  onLinkEdit,
  onMeetingClick,
  isWebApp,
  onScheduleMeeting,
}) => {
  const { formatMessage } = useIntl()

  const {
    attributes: {
      status,
      requestorInfo,
      locationPreference,
      timeRanges,
      proposedTimeZone,
      meetingsQuota,
      isUnlimitedUse,
      preferredVconLink,
      relationships: { topic, actions, engagementType, attendees },
    },
  } = linkDetails

  let primaryActions = {}
  let secondaryActions = actions

  const externalAttendees = attendees.filter(({ type }) => type === 'external')

  if (status === 'pending') {
    const { canRemindParticipants, ...rest } = actions
    primaryActions = {
      canRemindParticipants:
        canRemindParticipants && !!externalAttendees.length,
    }
    secondaryActions = rest
  } else if (status === 'expired' || status === 'cancelled') {
    const { canRenewExpiry, ...rest } = actions
    primaryActions = { canRenewExpiry }
    secondaryActions = rest
  } else if (status === 'scheduled') {
    const { canEdit, ...rest } = actions
    primaryActions = { canEdit }
    secondaryActions = rest
  }

  // TODO: Hide edit on web app till edit on webapp is introduced
  if (isWebApp) {
    primaryActions = { ...primaryActions, canEdit: false }
    secondaryActions = { ...secondaryActions, canEdit: false }
  }

  return (
    <LinkDetailsRoot>
      <Header
        linkDetails={linkDetails}
        secondaryActions={secondaryActions}
        onLinkEdit={onLinkEdit}
        onScheduleMeeting={onScheduleMeeting}
      />
      <LinkDetailsContent>
        <LinkStatus linkDetails={linkDetails} onMeetingClick={onMeetingClick} />
        <LinkInfo Icon={MeetingsOutlined}>
          <InfoLabel>{engagementType.displayName}</InfoLabel>
        </LinkInfo>
        {topic && (
          <LinkInfo Icon={TopicOutlined}>
            <InfoLabel>{topic.displayName}</InfoLabel>
          </LinkInfo>
        )}
        <AvailableSlots timeRanges={timeRanges} timeZone={proposedTimeZone} />
        {requestorInfo && (
          <LinkInfo Icon={Message}>
            <InfoLabel>{requestorInfo.name}</InfoLabel>
            <InfoDescription>
              {formatMessage(messages.requestorLabel)}
            </InfoDescription>
          </LinkInfo>
        )}
        {locationPreference && (
          <LinkInfo Icon={LocationOutlined}>
            {locationPreference.locationType === 'offsite' ? (
              <InfoLabel>
                {locationPreference.offsiteLocation?.name}&nbsp;
                {locationPreference.offsiteLocation?.address}
              </InfoLabel>
            ) : (
              <>
                <InfoLabel>
                  {formatMessage(roomLabels[locationPreference.locationType])}
                </InfoLabel>
                <InfoDescription>
                  {formatMessage(messages.preferredLabel)}
                </InfoDescription>
              </>
            )}
          </LinkInfo>
        )}
        {preferredVconLink && (
          <LinkInfo Icon={VideoOutlined}>
            <InfoLabel>{preferredVconLink.conferenceName}</InfoLabel>
            <InfoDescription>
              {formatMessage(messages.preferredLabel)}
            </InfoDescription>
          </LinkInfo>
        )}
        <LinkInfo Icon={LinkIcon}>
          <InfoLabel>
            {isUnlimitedUse
              ? formatMessage(messages.unlimitedUseLabel)
              : formatMessage(messages.meetingsQuotaLabel, { meetingsQuota })}
          </InfoLabel>
        </LinkInfo>
        <Tabs linkDetails={linkDetails} />
      </LinkDetailsContent>
      <FooterAction
        externalAttendees={externalAttendees}
        linkDetails={linkDetails}
        primaryActions={primaryActions}
        onLinkEdit={onLinkEdit}
      />
    </LinkDetailsRoot>
  )
}

const LinkDetails: VFC<LinkDetailsProps> = ({ link, ...rest }) => {
  const {
    id: token,
    relationships: {
      location: { systemName: eventName },
    },
  } = link

  const summitClient = useSummitV1Client(eventName)

  const {
    isLoading,
    isFetching,
    isFetchedAfterMount,
    data,
    error,
    refetch,
  } = useQuery(
    ['sharedLinkDetails', { token }],
    getSharedLinkDetailsQueryFn(summitClient)
  )

  if (isLoading || (isFetching && isFetchedAfterMount)) {
    return <Loader />
  }
  if (error || !data) {
    return <ErrorPage onButtonClick={() => refetch()} />
  }

  return <LinkDetailsView linkDetails={data} {...rest} />
}

export const LinkDetailsDrawer: VFC<LinkDetailsDrawerProps> = ({
  onClose,
  ...restProps
}) => (
  <DrawerPane onClose={onClose} isWebApp={restProps.isWebApp}>
    <LinkDetails {...restProps} />
  </DrawerPane>
)
