import { Fragment, VFC } from 'react'
import { useQuery } from 'react-query'
import { getEngagementTypeCustomFormQueryFn, useSummitClient } from 'rest'
import { ModalSpinner } from '@jifflenow/brightui/modal-spinner'
import { ErrorPage } from 'ui-components/ErrorPage'
import {
  RequestFormFieldName,
  RequestFormFieldValue,
} from './styled-components'
import { MeetingInfoTabProps } from './types'

export const MeetingInfoTab: VFC<MeetingInfoTabProps> = props => {
  const {
    relationships: {
      location: { systemName: eventName },
      engagementType: { uuid },
    },
    customFields,
  } = props.attributes

  const client = useSummitClient()

  const { isLoading, isFetching, data, error, refetch } = useQuery(
    [
      'engagementTypeCustomForm',
      {
        path: {
          eventName,
        },
        query: {
          uuid,
        },
      },
    ],
    getEngagementTypeCustomFormQueryFn(client)
  )

  if (isLoading || isFetching) {
    return <ModalSpinner />
  }
  if (error || !data) {
    return <ErrorPage onButtonClick={() => refetch()} />
  }

  const meetingTypeName = Object.keys(data.data)[0]
  const fields =
    data.data[meetingTypeName].forms?.external_activity_custom_form || []

  const fieldLabels: Record<string, string> = fields.reduce(
    (acc, { field_name, label }) => ({ ...acc, [field_name]: label }),
    {}
  )

  return (
    <>
      {Object.keys(customFields ?? {}).map((key, i) => {
        const value = customFields![key]

        return (
          <Fragment key={i}>
            <RequestFormFieldName>{fieldLabels[key]}</RequestFormFieldName>
            <RequestFormFieldValue>
              {Array.isArray(value) ? (
                value.join(', ')
              ) : (
                <div dangerouslySetInnerHTML={{ __html: value }} />
              )}
            </RequestFormFieldValue>
          </Fragment>
        )
      })}
    </>
  )
}
