import { FC } from 'react'
import { Drawer } from '../Drawer'
import { getDrawerOverrides } from './overrides'

type DrawerPaneProps = {
  onClose: () => void
  isWebApp?: boolean
}

export const DrawerPane: FC<DrawerPaneProps> = ({
  children,
  onClose,
  isWebApp,
}) => (
  <Drawer
    size="100%"
    isOpen
    onClose={onClose}
    overrides={getDrawerOverrides(isWebApp)}
  >
    {children}
  </Drawer>
)
