import { stringifyUrl } from 'query-string'
import { components } from 'types/openapi'

type SharedLink = components['schemas']['SharedLink']

export const scheduleMeeting = (sharedLink: SharedLink) => {
  const {
    id: sharedLinkUuid,
    meetingWith: meeting_with,
    relationships: {
      location: { systemName: eventName, uuid: current_location_uuid },
      engagementType: { id: activity_uuid },
      attendees = [],
      topic: { uuid: topic_uuid } = {},
    },
  } = sharedLink

  window.open(
    stringifyUrl(
      {
        url: `${window.location.origin}/${eventName}/meeting_request/new`,
        query: {
          current_location_uuid,
          activity_uuid,
          meeting_with,
          sharedLinkUuid,
          'user_uuids[]': attendees.map(({ uuid }) => uuid),
          topic_uuid,
        },
      },
      { skipEmptyString: true }
    ),
    '_blank'
  )
}
