import { FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { Tag, VARIANT, KIND } from '@jifflenow/brightui/tag'
import { TagOverrides } from './tagOverrides'
import { StatusCellProps, Status } from './types'

const messages = defineMessages({
  pending: {
    defaultMessage: 'Pending',
    description: 'Grid cell value',
  },
  scheduled: {
    defaultMessage: 'Scheduled',
    description: 'Grid cell value',
  },
  expired: {
    defaultMessage: 'Expired',
    description: 'Grid cell value',
  },
  cancelled: {
    defaultMessage: 'Canceled',
    description: 'Grid cell value',
  },
})

const kind: Record<Status, keyof typeof KIND> = {
  pending: 'warning',
  expired: 'negative',
  scheduled: 'positive',
  cancelled: 'neutral',
}

export const StatusCell: FC<StatusCellProps> = ({ value }) => {
  const { formatMessage } = useIntl()

  return (
    <Tag
      closeable={false}
      variant={VARIANT.outlined}
      overrides={TagOverrides}
      kind={kind[value]}
    >
      {formatMessage(messages[value as keyof typeof messages])}
    </Tag>
  )
}
