import { FC } from 'react'
import { defineMessages, useIntl } from 'react-intl'
import { TextFilter } from '@jifflenow/brightui/filter'
import { FilterItem } from 'components/ag-grid'
import { components } from 'types/openapi'
import { StaticFilterProps } from './types'

type InboundLinkType = components['schemas']['InboundLinkType']

const labels = defineMessages<InboundLinkType>({
  singleUse: {
    defaultMessage: 'Single use',
    description: 'Options label',
  },
  multiUse: {
    defaultMessage: 'Multi use',
    description: 'Options label',
  },
})

const filterValues: InboundLinkType[] = ['singleUse', 'multiUse']

export const TypeFilter: FC<StaticFilterProps> = ({
  onChange,
  value,
  label,
}) => {
  const { formatMessage } = useIntl()

  const mapper = (id: string) => ({
    id,
    label: formatMessage(labels[id as keyof typeof labels]),
  })

  return (
    <FilterItem>
      <TextFilter
        label={label}
        value={value.map(mapper)}
        onChange={onChange}
        options={filterValues.map(mapper)}
      />
    </FilterItem>
  )
}
